<template>
    <div>
        <el-container id="el-container-reservoirInfo">
            <el-header>
                水文信息
            </el-header>
            <el-main>
                <el-row style="margin-bottom: 5px" :gutter="10">
                    <el-col :span="6">
                        <el-card shadow="hover" :body-style="{
                           flex:1,
                           position: 'relative',
                        }">
                            <el-form label-width="40%"
                                     style="position: absolute;top: 1em;left: 1em;right: 1em;bottom:1em"
                                     id="reservoirInfo-el-form11">
                                <el-form-item style="margin-bottom: 0" label="水库名：">
                                    {{selectedReservoir}}
                                </el-form-item>
                                <el-form-item style="margin-bottom: 0" label="日期：">
                                    <span v-text="selectedDate"></span>
                                </el-form-item>
                                <el-form-item style="margin-bottom: 0" label="实测水位：">
                                    <span
                                            v-text="selectIsMeasuredLevelFitting?selectMeasuredLevel+' *':selectMeasuredLevel"
                                            style="color: #c12e34"></span>
                                </el-form-item>
                                <el-form-item style="margin-bottom: 0" label="遥测水位：">

                                    <span v-text="selectIsRadarLevelFitting?selectRadarLevel+' *':selectRadarLevel"
                                          style="color: #0098d9"></span>
                                </el-form-item>
                            </el-form>

                        </el-card>
                    </el-col>
                    <el-col :span="10">
                        <el-card shadow="hover" :body-style="{position:'relative', height: '100%'}">
                            <div slot="header">
                                <span>{{tableShowing==='area'?'水域面积结果一览':'蓄水量结果一览'}}</span>
                                <el-button style="float: right; padding: 3px 0" type="text" @click="switchTableShowing">
                                    {{tableShowing==='area'?'查看蓄水量':'查看水域面积'}}
                                </el-button>
                            </div>
                            <el-table
                                    :data="tableData.area"
                                    :height="'101%'"
                                    v-if="tableShowing==='area'"
                                    border
                                    @cell-mouse-enter="handleCellMouseEnter13"
                                    @cell-mouse-leave="handleCellMouseLeave13"
                            >
                                <el-table-column
                                        prop="levelName"
                                        label="水位来源"
                                        header-align="center"
                                        align="center"></el-table-column>
                                <el-table-column
                                        prop="level"
                                        label="水位值"
                                        header-align="center"
                                        align="center">
                                    <template slot-scope="scope">
                                        <span v-text="scope.row.level" :style="{color:scope.row.levelColor}"></span>
                                    </template>
                                </el-table-column>
                                <el-table-column
                                        prop="areaModelName"
                                        label="面积模型"
                                        header-align="center"
                                        align="center"></el-table-column>
                                <el-table-column
                                        label="面积值"
                                        header-align="center"
                                        align="center">
                                    <template slot-scope="scope">
                                        <span v-text="scope.row.area" :style="{color:scope.row.areaColor}"></span>
                                    </template>
                                </el-table-column>
                            </el-table>
                            <el-table
                                    :data="tableData.storage"
                                    :height="'101%'"
                                    v-if="tableShowing==='storage'"
                                    border
                                    @cell-mouse-enter="handleCellMouseEnter22"
                                    @cell-mouse-leave="handleCellMouseLeave22"
                            >
                                <el-table-column
                                        prop="levelName"
                                        label="水位来源"
                                        header-align="center"
                                        align="center"></el-table-column>
                                <el-table-column
                                        prop="level"
                                        label="水位值"
                                        header-align="center"
                                        align="center">
                                    <template slot-scope="scope">
                                        <span v-text="scope.row.level" :style="{color:scope.row.levelColor}"></span>
                                    </template>
                                </el-table-column>
                                <el-table-column
                                        prop="storageModelName"
                                        label="蓄水量模型"
                                        header-align="center"
                                        align="center"></el-table-column>
                                <el-table-column
                                        label="蓄水量"
                                        header-align="center"
                                        align="center">
                                    <template slot-scope="scope">
                                        <span v-text="scope.row.storage" :style="{color:scope.row.storageColor}"></span>
                                    </template>
                                </el-table-column>
                            </el-table>
                        </el-card>
                    </el-col>
                    <el-col :span="8">
                        <el-card shadow="hover" :body-style="{
                           flex:1,
                           position: 'relative',
                        }">
                            <div slot="header">
                                <span>水域面积</span>
                                <el-button style="float: right; padding: 3px 0" type="text">模型设置</el-button>
                            </div>
                            <div id="chart13" class="charts"></div>
                        </el-card>
                    </el-col>
                </el-row>
                <el-row style="margin-top: 5px" :gutter="10">
                    <el-col :span="16">
                        <el-card shadow="hover" :body-style="{
                           flex:1,
                           position: 'relative',
                        }">
                            <div slot="header">
                                <span>水位高度</span>
                                <el-button style="float: right; padding: 3px 0" type="text" @click="modelData.level.show=true">模型设置</el-button>
                                <el-popover v-model="popoverVisible.level" width="150">
                                    <p>请输入水位高度：</p>
                                    <el-input placeholder="请输入" size="small" v-model="popoverInputLevel">
                                        <template slot="append">米</template>
                                    </el-input>
                                    <div style="text-align: right; margin: 1em 0 0;">
                                        <el-button type="primary" size="small" @click="handlePopoverButtonClick">确定
                                        </el-button>
                                    </div>
                                    <el-button slot="reference" type="text"
                                               style="float: right; padding: 3px 0;margin-right: 1em">设置水位
                                    </el-button>
                                </el-popover>
                                <el-popover v-model="popoverVisible.date">
                                    <el-date-picker
                                            v-model="dateRange"
                                            type="daterange"
                                            range-separator="至"
                                            start-placeholder="开始日期"
                                            end-placeholder="结束日期"
                                            value-format="timestamp"
                                            unlink-panels>
                                    </el-date-picker>
                                    <div style="text-align: right; margin: 1em 0 0;">
                                        <el-button type="primary" size="small" @click="handleDateRangeClick">确定
                                        </el-button>
                                    </div>
                                    <el-button slot="reference" type="text"
                                               style="float: right; padding: 3px 0;margin-right: 1em">设置日期范围
                                    </el-button>
                                </el-popover>
                            </div>
                            <div id="chart21" class="charts"></div>
                        </el-card>
                    </el-col>
                    <el-col :span="8">
                        <el-card shadow="hover" :body-style="{
                           flex:1,
                           position: 'relative',
                        }">
                            <div slot="header">
                                <span>蓄水量</span>
                                <el-button style="float: right; padding: 3px 0" type="text">模型设置</el-button>
                            </div>
                            <div id="chart22" class="charts"></div>
                        </el-card>
                    </el-col>
                </el-row>
            </el-main>
        </el-container>
        <el-dialog title="水位模型设置" :visible.sync="modelData.level.show">
            <el-table :data="modelData.level.models">
                <el-table-column property="name" label="模型名称" width="100"></el-table-column>
                <el-table-column label="开始日期" width="150">
                    <template slot-scope="scope">
                        <span>{{getDateStringFromTimestamp(scope.row.firstDate)}}</span>
                    </template>
                </el-table-column>
                <el-table-column property="orders" label="参数（最左为0阶）"></el-table-column>
            </el-table>
        </el-dialog>
    </div>
</template>

<script>

    export default {
        name: "reservoirInformation",
        data: function () {
            return {
                charts: {},
                chartsOld: {
                    chart13: {
                        obj: {},
                        option: {
                            "color": [
                                "#32a487",
                                "#b6a2de",
                                "#5ab1ef",
                                "#ffb980",
                                "#d87a80",
                                "#8d98b3",
                                "#e5cf0d",
                                "#97b552",
                            ],
                            legend: {
                                data: [],
                            },
                            grid: {
                                top: '35px',
                                bottom: '30px',
                                left: '60px',
                                right: '60px',
                            },
                            dataset: {
                                dimensions: ['level'],
                                source: [],

                            },
                            dataZoom: [
                                {
                                    type: 'inside',
                                    realtime: true,
                                }],
                            tooltip: {
                                show: true,
                                trigger: 'axis'
                            },
                            xAxis: {
                                name: '水位/m',
                                type: 'value',
                                scale: true,
                            },
                            yAxis: {
                                name: '水域面积',
                                type: 'value',
                                scale: true,
                            },
                            series: [],
                        },
                        markLineOption: {
                            series: [{
                                markLine: {
                                    silent: true,
                                    symbol: 'none',
                                    label: {
                                        show: false,
                                    }
                                },
                            }],
                        },
                    },
                    chart21: {
                        obj: {},
                        option: {
                            "color": [
                                "#c12e34",
                                "#0098d9",
                                "#e6b600",
                                "#2b821d",
                                "#005eaa",
                                "#339ca8",
                                "#cda819",
                                "#32a487"
                            ],
                            legend: {
                                right: '5px',
                                top: '30',
                                height: '80',
                                orient: 'vertical',
                                type: 'scroll',
                                data: ['实测水位', '雷达高度计'],
                            },
                            grid: {
                                left: '50px',
                                top: '30px',
                                bottom: '70px',
                                right: '120px',
                            },
                            dataset: {
                                dimensions: [
                                    'date',
                                    '实测水位',
                                    'isMeasuredLevelFitting',
                                    '雷达高度计',
                                    'isMeasuredLevelFitting'
                                ],
                                source: [],

                            },
                            dataZoom: [
                                {
                                    show: true,
                                },
                                {
                                    type: 'inside',
                                    realtime: true,
                                }],
                            toolbox: {
                                showTitle: false,
                                feature: {
                                    magicType: {
                                        type: ['line', 'bar']
                                    },
                                },
                            },
                            tooltip: {
                                show: true,
                                trigger: 'axis'
                            },
                            xAxis: {
                                name: '日期',
                                type: 'time',
                            },
                            yAxis: {
                                name: '水位/m',
                                type: 'value',
                                scale: true,
                            },
                            series: [{
                                name: '实测水位',
                                type: 'line',
                                encode: {
                                    x: 'date',
                                    y: '实测水位',
                                },
                                showSymbol: false,
                                markLine: {
                                    data: [],
                                    lineStyle: {
                                        color: '#ae25b2',
                                        type: 'solid',
                                        width: 2,
                                    },
                                    symbol: 'none',
                                }
                            },
                                {
                                    name: '雷达高度计',
                                    type: 'line',
                                    encode: {
                                        x: 'date',
                                        y: '雷达高度计',
                                    },
                                    showSymbol: false,
                                },
                            ],
                        },
                        markLineOption: {
                            series: [{
                                markLine: {
                                    data: [],
                                }
                            },
                            ],
                        },
                    },
                    chart22: {
                        obj: {},
                        option: {
                            "color": [
                                "#516b91",
                                "#59c4e6",
                                "#edafda",
                                "#93b7e3",
                                "#a5e7f0",
                                "#cbb0e3",
                                "#005eaa",
                                "#339ca8",
                            ],
                            legend: {
                                data: [],
                            },
                            grid: {
                                top: '35px',
                                bottom: '30px',
                                left: '60px',
                                right: '60px',
                            },
                            dataset: {
                                dimensions: ['level'],
                                source: [],

                            },
                            dataZoom: [
                                {
                                    type: 'inside',
                                    realtime: true,
                                }],
                            tooltip: {
                                show: true,
                                trigger: 'axis'
                            },
                            xAxis: {
                                name: '水位/m',
                                type: 'value',
                                scale: true,
                            },
                            yAxis: {
                                name: '蓄水量',
                                type: 'value',
                                scale: true,
                            },
                            series: [],
                        },
                        markLineOption: {
                            series: [{
                                markLine: {
                                    silent: true,
                                    symbol: 'none',
                                    label: {
                                        show: false,
                                    },
                                }
                            },
                            ],
                        },
                    },
                },
                chartsData: {
                    measuredResult: [],
                    radarResult: [],
                    measuredModel:{
                        params:[],
                        firstDate:'',
                    },
                    radarModel:{
                        params:[],
                        firstDate:'',
                    },
                    levelModels: [],
                    areaModels: [],
                    storageModels: [],
                },
                currentLevel: [],
                dateRange:null,
                hasAddedCustomLevel: false,
                maxLevel: '',
                minLevel: '',
                modelData:{
                    level:{
                        show: false,
                        models:[],
                    },
                    area:{
                        show: false,
                        models:[],
                    },
                    storage:{
                        show: false,
                        models:[],
                    },
                },
                popoverVisible: {
                    date: false,
                    level: false,
                },
                popoverInputLevel: '',
                selectedReservoir: '丹江口水库',
                selectedDate: '',
                selectMeasuredLevel: '-',
                selectIsMeasuredLevelFitting: false,
                selectRadarLevel: '-',
                selectIsRadarLevelFitting: false,
                tableData: {
                    area: [],
                    storage: [],
                },
                tableShowing: 'area',
                waterLevelDataSet: [],
                waterAreaDataSet: [],
                waterStorageDataSet: [],
            }
        },
        methods: {
            echartsInit: function () {
                //克隆对象
                this.charts = JSON.parse(JSON.stringify(this.chartsOld));
                //初始化echarts元素
                this.charts.chart13.obj = this.$echarts.init(document.getElementById('chart13'));
                this.charts.chart21.obj = this.$echarts.init(document.getElementById('chart21'));
                this.charts.chart22.obj = this.$echarts.init(document.getElementById('chart22'));
                //test
                const self = this;
                this.echartsSetFirstOption();
                let timeout;
                self.charts.chart21.obj.on('updateAxisPointer', function (p) {
                    clearTimeout(timeout);
                    timeout = setTimeout(self.timeoutFunction, 200, p)
                });
                this.charts.chart21.obj.on('click', function (p) {
                    if (p.componentType === 'markLine') {
                        for (let i = 0; i < self.charts.chart21.option.legend.data.length; i++) {
                            self.charts.chart21.markLineOption.series[i].markLine.data = [];
                        }
                        self.charts.chart21.obj.setOption(self.charts.chart21.markLineOption);
                        self.charts.chart21.obj.on('updateAxisPointer', function (p) {
                            clearTimeout(timeout);
                            timeout = setTimeout(self.timeoutFunction, 200, p)
                        });
                    } else {
                        self.formSetData(p.data);
                        self.showMarkLine(p.data);
                        self.setTableData();
                        self.charts.chart21.obj.off('updateAxisPointer');
                        for (let i = 0; i < self.charts.chart21.option.legend.data.length; i++) {
                            self.charts.chart21.markLineOption.series[i] = {
                                markLine: {
                                    name: 'levelMarkLine',
                                    data: [{
                                        xAxis: p.data['date'],
                                    }],
                                    lineStyle: {
                                        color: '#ae25b2',
                                        type: 'solid',
                                        width: 1.5,
                                    },
                                    symbol: 'none',
                                }
                            }
                        }

                        self.charts.chart21.obj.setOption(self.charts.chart21.markLineOption);
                    }
                });

            },

            timeoutFunction: function (p) {
                if (p.axesInfo.length > 0) {
                    this.formSetData(this.waterLevelDataSet[p.dataIndex]);
                    this.showMarkLine(this.waterLevelDataSet[p.dataIndex]);
                    this.setTableData();
                }
            },

            formSetData: function (data) {
                //把水位数据取到model中
                const date = data.date.toLocaleString();
                this.selectedDate = date.substring(0, date.indexOf(' '));
                this.selectIsMeasuredLevelFitting = false;
                this.selectIsRadarLevelFitting = false;
                this.selectMeasuredLevel = '-';
                this.selectRadarLevel = '-';
                const legendData = this.charts.chart21.option.legend.data;
                const option = this.charts.chart21.obj.getOption();
                this.currentLevel = [];
                for (let i = 0; i < legendData.length; i++) {
                    if (option.legend[0].selected[legendData[i]] !== false) {
                        if (legendData[i] === '实测水位' && typeof(data['实测水位'])!=='undefined') {
                            this.selectMeasuredLevel = data['实测水位'] + 'm';
                            this.selectIsMeasuredLevelFitting = data.isMeasuredLevelFitting;
                            this.currentLevel.push({name: legendData[i], level: data[legendData[i]]});
                        } else if (legendData[i] === '雷达高度计' && typeof(data['雷达高度计'])!=='undefined') {
                            this.selectRadarLevel = data['雷达高度计'] + 'm';
                            this.selectIsRadarLevelFitting = data.isRadarLevelFitting;
                            this.currentLevel.push({name: legendData[i], level: data[legendData[i]]});
                        }   else if (typeof(data[legendData[i]])!=='undefined'){
                            this.currentLevel.push({name: legendData[i], level: data[legendData[i]]});
                        }
                    }

                }
            },

            showMarkLine: function (data) {
                let markLineData = [];
                const option = this.charts.chart21.obj.getOption();
                const legendData = this.charts.chart21.option.legend.data;
                for (let i = 0; i < legendData.length; i++) {
                    if (option.legend[0].selected[legendData[i]] !== false && typeof(data[legendData[i]])!=='undefined') {
                        markLineData.push({
                            xAxis: data[legendData[i]],
                            lineStyle: {
                                color: option.color[i],
                                type: 'solid',
                                width: 1.5,
                            },
                        });
                    }
                }
                if (this.chartsData.areaModels.length>0){
                    for (let i = 0; i < this.charts.chart13.option.legend.data.length; i++) {
                        this.charts.chart13.markLineOption.series[i] = {
                            markLine: {
                                data: markLineData,
                                silent: true,
                                symbol: 'none',
                                label: {
                                    show: false,
                                },
                            }
                        }
                    }
                    this.charts.chart13.obj.setOption(this.charts.chart13.markLineOption);
                }
                if (this.chartsData.storageModels.length>0){
                    for (let i = 0; i < this.charts.chart22.option.legend.data.length; i++) {
                        this.charts.chart22.markLineOption.series[i] = {
                            markLine: {
                                data: markLineData,
                                silent: true,
                                symbol: 'none',
                                label: {
                                    show: false,
                                },
                            }
                        }
                    }
                    this.charts.chart22.obj.setOption(this.charts.chart22.markLineOption);
                }


            },

            echartsSetFirstOption: function () {
                this.waterLevelDataSet = this.getWaterLevelDataList(
                    this.chartsData.measuredResult,
                    this.chartsData.radarResult,
                    this.chartsData.measuredModel,
                    this.chartsData.radarModel,
                );
                this.charts.chart21.option.dataset.source = this.waterLevelDataSet;
                for (let i = 0; i < this.chartsData.levelModels.length; i++) {
                    const name = this.chartsData.levelModels[i].name;
                    this.charts.chart21.option.legend.data.push(name);
                    this.charts.chart21.option.dataset.dimensions.push(name);
                    this.charts.chart21.option.series.push({
                        name: name,
                        type: 'line',
                        encode: {
                            x: 'date',
                            y: name,
                        },
                        showSymbol: false,
                    })
                }
                this.getTwoDataLists(
                    this.minLevel,
                    this.maxLevel,
                    this.waterLevelDataSet,
                    this.chartsData.areaModels,
                    this.chartsData.storageModels
                );
                this.getTwoOptions();
                this.charts.chart21.obj.setOption(this.charts.chart21.option);
                this.charts.chart13.obj.setOption(this.charts.chart13.option);
                this.charts.chart22.obj.setOption(this.charts.chart22.option);
            }
            ,

            getTwoDataLists: function (min, max, waterLevelList, areaModels, storageModels) {
                let areaList = [];
                let storageList = [];
                for (let i = min; i <= max; i++) {
                    let obj_a = {level: i};
                    let obj_s = {level: i};
                    for (let p = 0; p < areaModels.length; p++) {
                        obj_a[areaModels[p].name] = this.getResultFromFittingParams(i, areaModels[p].params, 2);
                    }
                    for (let p = 0; p < storageModels.length; p++) {
                        obj_s[storageModels[p].name] = this.getResultFromFittingParams(i, storageModels[p].params, 2);
                    }
                    areaList.push(obj_a);
                    storageList.push(obj_s);
                }
                const legendData = this.charts.chart21.option.legend.data;
                for (let x = 0; x < legendData.length; x++) {
                    for (let i = 0; i < waterLevelList.length; i++) {
                        let obj_a = {level: waterLevelList[i][legendData[x]]};
                        let obj_s = {level: waterLevelList[i][legendData[x]]};
                        for (let p = 0; p < areaModels.length; p++) {
                            obj_a[areaModels[p].name] = this.getResultFromFittingParams(waterLevelList[i][legendData[x]], areaModels[p].params, 2);
                        }
                        for (let p = 0; p < storageModels.length; p++) {
                            obj_s[storageModels[p].name] = this.getResultFromFittingParams(waterLevelList[i][legendData[x]], storageModels[p].params, 2);
                        }
                        areaList.push(obj_a);
                        storageList.push(obj_s);
                    }
                }
                //删除重复水位项
                let areaListAfter = [];
                let storageListAfter = [];
                for (let i = 0, l = areaList.length; i < l; i++) {
                    for (let j = i + 1; j < l; j++) {
                        if (+areaList[i].level === +areaList[j].level) j = ++i;
                    }
                    areaListAfter.push(areaList[i]);
                    storageListAfter.push(storageList[i]);
                }
                //按水位排序
                const compare = (obj1, obj2) => {
                    const [level1, level2] = [obj1.level, obj2.level];
                    if (level1 < level2) return -1;
                    else if (level1 > level2) return 1;
                    else return 0;
                };
                this.waterAreaDataSet = areaListAfter.sort(compare);
                this.waterStorageDataSet = storageListAfter.sort(compare);
            }
            ,

            getTwoOptions: function () {
                let areaOption = this.charts.chart13.option;
                for (let i = 0; i < this.chartsData.areaModels.length; i++) {
                    const name = this.chartsData.areaModels[i].name;
                    areaOption.legend.data.push(name);
                    areaOption.dataset.dimensions.push(name);
                    areaOption.series.push({
                            name: name,
                            type: 'line',
                            encode: {
                                x: 'level',
                                y: name,
                            },
                            showSymbol: false,
                        }
                    )
                }
                areaOption.dataset.source = this.waterAreaDataSet;

                let storageOption = this.charts.chart22.option;
                for (let i = 0; i < this.chartsData.storageModels.length; i++) {
                    const name = this.chartsData.storageModels[i].name;
                    storageOption.legend.data.push(name);
                    storageOption.dataset.dimensions.push(name);
                    storageOption.series.push({
                            name: name,
                            type: 'line',
                            encode: {
                                x: 'level',
                                y: name,
                            },
                            showSymbol: false,
                        }
                    )
                }
                storageOption.dataset.source = this.waterStorageDataSet;
            }
            ,

            getWaterLevelDataList: function (measuredList, radarList, measuredModel, radarModel) {
                //获取日期范围内每一天的水位表，同时得到最大、最小水位
                let [measuredIndex, radarIndex] = [0, 0];
                let [maxLevel, minLevel] = [-1,9999999999];
                let waterLevelDataSet = [];
                console.log(this.dateRange);
                const hasDataRange = (null !== this.dateRange);
                let startDate_M = hasDataRange?new Date(Math.max(measuredList[0].date,this.dateRange[0])):new Date(measuredList[0].date);
                let endDate_M = hasDataRange?new Date(Math.min(measuredList[measuredList.length-1].date,this.dateRange[1])):new Date(measuredList[measuredList.length-1].date);
                if (startDate_M.valueOf() < endDate_M){
                    endDate_M.setDate(endDate_M.getDate()+1);
                    maxLevel = this.getResultFromFittingParams(this.getDaysFromDate(startDate_M, measuredModel.firstDate), measuredModel.params, 2);
                    minLevel = maxLevel;
                    for (; !this.isSameDate(startDate_M, endDate_M); startDate_M.setDate(startDate_M.getDate() + 1)) {
                        let waterLevelItem = {date: new Date(startDate_M.valueOf())};
                        let mlevel = Number();
                        if (measuredIndex <= measuredList.length) {
                            if (this.isSameDate(measuredList[measuredIndex].date, startDate_M)) {
                                mlevel = Number(measuredList[measuredIndex].level).toFixed(2);
                                waterLevelItem['实测水位'] = mlevel;
                                waterLevelItem.isMeasuredLevelFitting = false;
                                measuredIndex++;

                            } else {
                                mlevel = this.getResultFromFittingParams(this.getDaysFromDate(startDate_M, measuredModel.firstDate), measuredModel.params, 2);
                                waterLevelItem['实测水位'] = mlevel;
                                waterLevelItem.isMeasuredLevelFitting = true;
                            }
                        } else {
                            mlevel = this.getResultFromFittingParams(this.getDaysFromDate(startDate_M, measuredModel.firstDate), measuredModel.params, 2);
                            waterLevelItem['实测水位'] = mlevel;
                            waterLevelItem.isMeasuredLevelFitting = true;
                        }
                        if (mlevel > maxLevel) maxLevel = mlevel;
                        else if (mlevel < minLevel) minLevel = mlevel;
                        waterLevelDataSet.push(waterLevelItem);
                    }
                }

                    // ------------------------《雷达》
                let startDate_R =  hasDataRange?new Date(Math.max(radarList[0].date,this.dateRange[0])):new Date(radarList[0].date);
                let endDate_R =  hasDataRange?new Date(Math.min(radarList[radarList.length-1].date,this.dateRange[1])):new Date(radarList[radarList.length-1].date);
                if (startDate_R.valueOf()<endDate_R.valueOf()){
                    endDate_R.setDate(endDate_R.getDate()+1);
                    maxLevel = Math.max(maxLevel, this.getResultFromFittingParams(this.getDaysFromDate(endDate_R, radarModel.firstDate), radarModel.params, 2));
                    minLevel = Math.min(minLevel, this.getResultFromFittingParams(this.getDaysFromDate(startDate_R, radarModel.firstDate), radarModel.params, 2));
                    for (; !this.isSameDate(startDate_R, endDate_R); startDate_R.setDate(startDate_R.getDate() + 1)) {
                        let index = -1;
                        let waterLevelItem = {};
                        for (let i = 0; i<waterLevelDataSet.length;i++){
                            if (this.isSameDate(startDate_R, waterLevelDataSet[i].date)){
                                index = i;

                                break;
                            }
                        }
                        waterLevelItem = index===-1?{date: new Date(startDate_R.valueOf())}:waterLevelDataSet[index];
                        let rlevel = 0;
                        if (radarIndex < radarList.length) {
                            if (this.isSameDate(radarList[radarIndex].date, startDate_R)) {
                                rlevel = Number(radarList[radarIndex].level).toFixed(2);
                                waterLevelItem['雷达高度计'] = rlevel;
                                waterLevelItem.isRadarLevelFitting = false;
                                radarIndex++;
                            } else {
                                rlevel = this.getResultFromFittingParams(this.getDaysFromDate(startDate_R, radarModel.firstDate), radarModel.params, 2);
                                waterLevelItem['雷达高度计'] = rlevel;
                                waterLevelItem.isRadarLevelFitting = true;
                            }
                        } else {
                            rlevel = this.getResultFromFittingParams(this.getDaysFromDate(startDate_R, radarModel.firstDate), radarModel.params, 2);
                            waterLevelItem['雷达高度计'] = rlevel;
                            waterLevelItem.isRadarLevelFitting = true;
                        }

                        if (rlevel > maxLevel) maxLevel = rlevel;
                        else if (rlevel < minLevel) minLevel = rlevel;
                        if (index===-1){
                            waterLevelDataSet.push(waterLevelItem);
                        }
                        // ------------------------《/雷达》
                    }
                }
                    //----------------------------------------------------------
                for (let p = 0; p < this.chartsData.levelModels.length; p++) {
                    let startDate_O = hasDataRange?new Date(Math.max(this.chartsData.levelModels[p].firstDate,this.dateRange[0])):new Date(this.chartsData.levelModels[p].firstDate);
                    let endDate_O = hasDataRange?new Date(Math.min(this.dateRange[1],new Date().valueOf())):new Date();
                    console.log(endDate_O)
                    if (startDate_O < endDate_O){
                        endDate_O.setDate(endDate_O.getDate()+1);
                        for (; !this.isSameDate(startDate_O, endDate_O); startDate_O.setDate(startDate_O.getDate() + 1)) {
                            let index = -1;
                            let waterLevelItem = {};
                            for (let i = 0; i<waterLevelDataSet.length;i++){
                                if (this.isSameDate(startDate_O, waterLevelDataSet[i].date)){
                                    index = i;
                                    break;
                                }
                            }
                            waterLevelItem = index===-1?{date:  new Date(startDate_O.valueOf())}:waterLevelDataSet[index];
                            let level = this.getResultFromFittingParams(this.getDaysFromDate(startDate_O, this.chartsData.levelModels[p].firstDate), this.chartsData.levelModels[p].params, 2);
                            waterLevelItem[this.chartsData.levelModels[p].name] = level;
                            if (level > maxLevel) maxLevel = level;
                            else if (level < minLevel) minLevel = level;
                            if (index===-1){
                                waterLevelDataSet.push(waterLevelItem);
                            }
                        }
                    }

                }
                this.maxLevel = Math.ceil(maxLevel);
                this.minLevel = Math.floor(minLevel);
                const compare = (obj1, obj2) => {
                    const [d1, d2] = [obj1.date.valueOf(), obj2.date.valueOf()];
                    if (d1 < d2) return -1;
                    else if (d1 > d2) return 1;
                    else return 0;
                };
                return waterLevelDataSet.sort(compare);

            }
            ,

            isSameDate: function (date1, date2) {
                return date1.getFullYear() === date2.getFullYear() && date1.getMonth() === date2.getMonth() && date1.getDate() === date2.getDate();
            }
            ,

            getDateFromStringDate: function (strDate) {
                //  "yyyy-mm-dd" => Date()
                if (strDate.constructor===Date){
                    return strDate;
                }
                const list = strDate.split('-');
                let date = new Date();
                date.setTime(0);
                date.setFullYear(list[0], list[1] - 1, list[2]);
                return date;
            }
            ,

            getResultFromFittingParams: function (x, params, fix) {
                let result = 0;
                for (let i = 0; i < params.length; i++) {
                    result += params[i] * Math.pow(x, i);
                }
                return result.toFixed(fix);
            }
            ,

            getDaysFromDate: function (date, firstDate) {
                return (date - firstDate) / (1000 * 60 * 60 * 24);
            }
            ,

            getAreaTableData: function (levelName, level, levelColor) {
                let list = [];
                const option = this.charts.chart13.obj.getOption();
                const legendData = this.charts.chart13.option.legend.data;
                for (let i = 0; i < legendData.length; i++) {
                    if (option.legend[0].selected[legendData[i]] !== false) {
                        let obj = {levelName: levelName, level: level, levelColor: levelColor};
                        obj.areaModelName = this.chartsData.areaModels[i].name;
                        obj.area = this.getResultFromFittingParams(level, this.chartsData.areaModels[i].params, 2);
                        obj.areaColor = this.charts.chart13.option.color[i];
                        list.push(obj);
                    }
                }
                return list;
            },

            getStorageTableData: function (levelName, level, levelColor) {

                let list = [];
                const option = this.charts.chart22.obj.getOption();
                const legendData = this.charts.chart22.option.legend.data;
                for (let i = 0; i < legendData.length; i++) {
                    if (option.legend[0].selected[legendData[i]] !== false) {
                        let obj = {levelName: levelName, level: level, levelColor: levelColor};
                        obj.storageModelName = this.chartsData.storageModels[i].name;
                        obj.storage = this.getResultFromFittingParams(level, this.chartsData.storageModels[i].params, 2);
                        obj.storageColor = this.charts.chart22.option.color[i];
                        list.push(obj);
                    }
                }
                return list;
            },

            setTableData: function () {
                let area = [];
                let storage = [];
                this.tableData = {};
                let levelColor = '';
                for (let i = 0; i < this.currentLevel.length; i++) {
                    const name = this.currentLevel[i].name;
                    const level = this.currentLevel[i].level;
                    for (let j = 0; j < this.charts.chart21.option.legend.data.length; j++) {
                        if (name === this.charts.chart21.option.legend.data[j]) {
                            levelColor = this.charts.chart21.option.color[j];
                            break;
                        }
                    }
                    if (this.chartsData.areaModels.length>0){
                        area = area.concat(this.getAreaTableData(name, level, levelColor));
                    }
                    if (this.chartsData.storageModels.length>0){
                        storage = storage.concat(this.getStorageTableData(name, level, levelColor));
                    }
                }
                this.tableData = {area: area, storage: storage};

            },

            handleCellMouseEnter13: function (p) {
                let index = -1;
                for (let i = 0; i < this.waterAreaDataSet.length; i++) {
                    if (this.waterAreaDataSet[i].level === p.level) {
                        index = i;
                        break;
                    }
                }
                this.charts.chart13.obj.dispatchAction({
                    type: 'highlight',
                    seriesName: p.areaModelName,
                    dataIndex: index,
                });
            },
            handleCellMouseLeave13: function () {
                this.charts.chart13.obj.dispatchAction({
                    type: 'downplay'
                });
            },

            handleCellMouseEnter22: function (p) {
                let index = -1;
                for (let i = 0; i < this.waterStorageDataSet.length; i++) {
                    if (this.waterStorageDataSet[i].level === p.level) {
                        index = i;
                        break;
                    }
                }
                this.charts.chart22.obj.dispatchAction({
                    type: 'highlight',
                    seriesName: p.storageModelName,
                    dataIndex: index,
                });
            },
            handleCellMouseLeave22: function () {
                this.charts.chart22.obj.dispatchAction({
                    type: 'downplay'
                });
            },

            switchTableShowing: function () {
                this.tableShowing = this.tableShowing === 'area' ? 'storage' : 'area';
            },

            handlePopoverButtonClick: function () {
                if (this.isPositiveNumber(this.popoverInputLevel)) {
                    if (!this.hasAddedCustomLevel) {
                        this.chartsData.levelModels.push({
                            name: '用户水位',
                            params: [this.popoverInputLevel],
                            firstDate: this.waterLevelDataSet[0].date.valueOf()
                        });
                        this.hasAddedCustomLevel = true;
                    } else {
                        this.chartsData.levelModels[this.chartsData.levelModels.length - 1] = {
                            name: '用户水位',
                            params: [this.popoverInputLevel],
                            firstDate: this.waterLevelDataSet[0].date.valueOf()
                        };
                    }
                    this.charts.chart13.obj.dispose();
                    this.charts.chart21.obj.dispose();
                    this.charts.chart22.obj.dispose();
                    this.echartsInit();
                    this.popoverVisible.level = false;
                } else {
                    console.error('error');
                }
            },

            handleDateRangeClick: function(){
                this.charts.chart13.obj.dispose();
                this.charts.chart21.obj.dispose();
                this.charts.chart22.obj.dispose();
                this.echartsInit();
                this.popoverVisible.date = false;
            },

            isNumber: function (val) {
                const regPos = /^\d+(\.\d+)?$/;
                const regNeg = /^(-(([0-9]+\.[0-9]*[1-9][0-9]*)|([0-9]*[1-9][0-9]*\.[0-9]+)|([0-9]*[1-9][0-9]*)))$/;
                return regPos.test(val) || regNeg.test(val);
            },

            isPositiveNumber: function (val) {
                const regPos = /^\d+(\.\d+)?$/;
                return regPos.test(val);
            },

            chartsResize: function () {
                this.charts.chart13.obj.resize();
                this.charts.chart21.obj.resize();
                this.charts.chart22.obj.resize();
            },

            getDateStringFromTimestamp: function (timestamp) {
                return new Date(timestamp).toLocaleDateString();
            },

            processChartsData: function (data) {
                for (let i = 0; i < data.measuredResult.length; i++){
                    this.chartsData.measuredResult.push({
                        date: new Date(data.measuredResult[i].date),
                        level: data.measuredResult[i].waterLevel,
                    })
                }
                for (let i = 0; i < data.radarResult.length; i++){
                    this.chartsData.radarResult.push({
                        date: new Date(data.radarResult[i].date),
                        level: data.radarResult[i].waterLevel,
                    })
                }
                this.chartsData.measuredModel = {
                    params: this.getArrayFromStringParams(data.measuredModel.orders),
                    firstDate: data.measuredModel.firstDate
                };
                this.chartsData.radarModel = {
                    params: this.getArrayFromStringParams(data.radarModel.orders),
                    firstDate: data.radarModel.firstDate
                };
                for (let i = 0; i<data.levelModel.length; i++){
                    const model = data.levelModel[i];
                    this.chartsData.levelModels.push({
                        name: model.name,
                        params: this.getArrayFromStringParams(model.orders),
                        firstDate: model.firstDate,
                    })
                }
                for (let i = 0; i<data.areaModel.length; i++){
                    const model = data.areaModel[i];
                    this.chartsData.areaModels.push({
                        name: model.name,
                        params: this.getArrayFromStringParams(model.orders),
                        firstDate: model.firstDate,
                    })
                }
                for (let i = 0; i<data.storageModel.length; i++){
                    const model = data.storageModel[i];
                    this.chartsData.storageModels.push({
                        name: model.name,
                        params: this.getArrayFromStringParams(model.orders),
                        firstDate: model.firstDate,
                    })
                }
            },

            getArrayFromStringParams: function (str) {
                const strArr = str.split(',');
                let arr = [];
                for (let i = 0; i < strArr.length; i++){
                    arr.push(strArr[i]*1);
                }
                return arr;
            }
        },
        mounted() {
            const self = this;
            this.axios.get('/api/getChartsData',{
                params:{
                    reservoirId: 3
                }
            }).then((response)=>{
                if (response.status === 200){
                    if (response.data !== 'error'){
                        self.processChartsData(response.data);
                        self.modelData.level.models = response.data.levelModel;
                        console.log(response.data.levelModel)
                        self.echartsInit();
                        let today = new Date();
                        const year = today.getFullYear();
                        const month = today.getMonth();
                        const date = today.getDate();
                        today.setTime(0);
                        today.setFullYear(year, month, date);
                        self.selectedDate = today.toLocaleDateString();
                        let ResizeSensor = require('@/js/ResizeSensor');
                        let timeout;
                        setTimeout(function () {
                            new ResizeSensor(document.querySelector('#el-container-reservoirInfo'), function () {
                                clearTimeout(timeout);
                                timeout = setTimeout(self.chartsResize, 50)
                            });
                        }, 5000);
                    }
                    else {
                        console.log('error');
                    }
                }
                else {
                    console.log('error');
                }
            });



        }
    }
</script>

